import Vue from 'vue'
import Router from 'vue-router'
import moment from 'moment'
import 'html5-reset/assets/css/reset.css'

// 承载
import testExperienceReportDetail from '../pages/testExperienceReportDetail.vue'
// 主页
import testExperienceReportDetailIndex from '../views/test-experience-report-detail.vue'

Vue.use(Router)

Vue.filter('dateFormat', function (value, type) { //时间格式化过滤
  return moment(value).format(type);
});

const router = new Router({
  routes: [{
    path: '/',
    name: 'testExperienceReportDetail',
    component: testExperienceReportDetailIndex
  }]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#testExperienceReportDetail',
  router,
  components: {
    testExperienceReportDetail
  },
  template: '<testExperienceReportDetail/>'
})



